*{
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-color: #cccccc;
    color: #000;;
}

a{
    text-decoration: none;
    color: #f7d354;
}

a:hover {
    color:#b8930c
}



.container .title {
    color: #000000;
    font-size: 2.5rem;
    text-align: center;
    margin: 2rem 0 1rem;
}

.title .query-text {
    color:#f7d354;
}

.movies-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
}

.movies-container div {
    width: 30%;
    color: #fff;
    margin-bottom: 2.5rem;
    display:flex;
    flex-direction: column;
    justify-content: space-between ;
    background-color: #111;
    padding: 1rem;
}

.movies-container div img {
    max-width: 100%;
}

.movies-container div img,
.movies-container div h2,
.movies-container div p {
    margin-bottom: 1rem;
}

.movies-container div svg {
    color:#f7d354;
}

.movies-container div a {
    background-color: #F7d354;
    border:2px solid #F7d354;
    border-radius: 4px;
    color:#000000;
    padding: 1rem 0.5rem;
    align-items: center;
    transition: 0.4s;
    text-align: center;
    font-weight: bold;
}

.movies-container div a:hover {
    background-color: transparent;
    color: #f7d354;
}